.searchContainer {
    display: flex;
    justify-content: center;
}

.searchBox {
    position: relative;
}

.searchButton {
    position: absolute;
    right: 8px;
    top: 0;
    height: 100%;
}

.searchInput {
    font-size: 1.2rem;
    height: 30px;
    border-radius: 10px;
    padding: 5px 30px 5px 10px;
    border: 2px solid white;
}

.searchInput:focus,
.searchButton:focus {
    outline: none;
    border: 2px solid #1525ff;
    box-shadow: 0 0 5px #ffffff;
}
